'use client';

import { useState } from 'react';
import { motion } from 'framer-motion';
import {
  Menu,
  PhoneCall,
  ChevronDown,
} from 'lucide-react';
import Image from 'next/image';
import Link from 'next/link';
import Logo from "@/asset/logo.png";

const servicesList = [
  "Air Freight",
  "Road Transportation",
  "Warehousing",
  "Custom Clearance",
  "Packaging Solutions",
];

export default function Header() {
  const [isOpen, setIsOpen] = useState(false);          // Mobile menu
  const [serviceOpen, setServiceOpen] = useState(false); // Mobile service dropdown
  const [hoverService, setHoverService] = useState(false); // Desktop hover dropdown

  return (
    <header className="bg-[#fff8f3] shadow-sm sticky top-0 z-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4 flex justify-between items-center">

        {/* Logo */}
        <Link href="/">
        Logo
        </Link>

        {/* Desktop Navigation */}
        <nav className="hidden md:flex space-x-8 text-sm font-medium text-gray-800 relative">

          <Link href="/" className="hover:text-red-600 transition text-red-600">
            Luggage
          </Link>

          <Link href="/about" className="hover:text-red-600 transition">
            About Us
          </Link>

          {/* Services Dropdown - Desktop */}
          <div
            className="relative"
            onMouseEnter={() => setHoverService(true)}
            onMouseLeave={() => setHoverService(false)}
          >
            <button className="flex items-center gap-1 hover:text-red-600 transition">
              Services <ChevronDown className="h-4 w-4" />
            </button>

            {hoverService && (
              <motion.div
                initial={{ opacity: 0, y: -5 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.2 }}
                className="absolute left-0 mt-0 w-52 bg-white shadow-lg rounded-lg py-3 z-50"
              >
                {servicesList.map((item, index) => (
                  <Link
                    key={index}
                    href={`/services/${item.toLowerCase().replace(/ /g, '-')}`}
                    className="block px-4 py-2 text-gray-700 hover:bg-red-50 hover:text-red-600 transition"
                  >
                    {item}
                  </Link>
                ))}
              </motion.div>
            )}
          </div>

          <Link href="/testimonials" className="hover:text-red-600 transition">
          Rate Calculator
          </Link>

          <Link href="/blog" className="hover:text-red-600 transition">
            FAQS
          </Link>

          <Link href="/contact" className="hover:text-red-600 transition">
            Contact Us
          </Link>
        </nav>

        {/* Desktop Contact */}
        <div className="hidden md:flex items-center space-x-6">
          <button className="bg-red-600 hover:bg-red-700 transition text-white px-4 py-2 rounded-full text-sm font-semibold">
            Tracking Now
          </button>
        </div>

        {/* Mobile Menu Button */}
        <div className="md:hidden">
          <button onClick={() => setIsOpen(!isOpen)}>
            <Menu className="h-6 w-6 text-red-600" />
          </button>
        </div>
      </div>

      {/* Mobile Dropdown Menu */}
      {isOpen && (
        <motion.div
          initial={{ height: 0 }}
          animate={{ height: "auto" }}
          transition={{ duration: 0.3 }}
          className="md:hidden bg-white overflow-hidden shadow-lg"
        >
          <div className="flex flex-col px-6 py-4 space-y-4">

            <Link href="/" className="hover:text-red-600">Home</Link>
            <Link href="/about" className="hover:text-red-600">About Us</Link>

            {/* Mobile Services Dropdown */}
            <button
              onClick={() => setServiceOpen(!serviceOpen)}
              className="flex justify-between items-center text-gray-800 font-medium"
            >
              Services
              <ChevronDown
                className={`h-4 w-4 transition-transform ${serviceOpen ? "rotate-180" : ""}`}
              />
            </button>

            {serviceOpen && (
              <div className="pl-4 flex flex-col space-y-2">
                {servicesList.map((item, i) => (
                  <Link
                    key={i}
                    href={`/services/${item.toLowerCase().replace(/ /g, '-')}`}
                    className="text-gray-700 hover:text-red-600 transition"
                  >
                    {item}
                  </Link>
                ))}
              </div>
            )}

            <Link href="/testimonials" className="hover:text-red-600">
              Testimonials
            </Link>

            <Link href="/blog" className="hover:text-red-600">
              Blog
            </Link>

            <Link href="/contact" className="hover:text-red-600">
              Contact Us
            </Link>

            

            <button className="bg-red-600 hover:bg-red-700 text-white px-4 py-2 rounded-full w-full">
              Tracking Now
            </button>
          </div>
        </motion.div>
      )}
    </header>
  );
}
